* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}
.wrap {
  width: 250px;
  margin: 0 auto;
  text-align: center;
}

input {
  padding-left: 10px;
  width: 100%;
  margin: 0 auto;
  text-align: left;
  height: 60px;
  font-size: 24px;
  border-radius: 5px;
  border: 1px solid #3700B3;
  background: none;
  outline: none;
  color: #3700B3;
}

input:hover {
  border: 3px solid  #03DAC6;
}

input:active {
  border: 3px solid #018786;
}

.wrap > * {
  margin: auto;
}

ul {
  padding: 0;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  list-style: none;
}

ul > * {
 width: 100%;
  height: 80px;
  border-bottom: 1px solid #747d8a;
  margin: auto;
}

.img {
  border-radius: 50%;
  margin-right: 10px;
  height: 32px;
  width: 32px;
}


li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: start;
  -ms-flex-align: center;
      align-items: center;
}

.card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.name {
  font-weight: bold;
  color: #000000;
}

.phone {
  color: #747d8a;
}

.address {
  font-size: 10px;
  color: #747d8a;
}



